import React from 'react';

const AboutMe = () => {
        return (
            <>
                <div id='aboutMe' >
                <h2>About Me</h2>
                <p> George Ingram is a Javascript developer and citizen of the United States of America. He is pragmatic in his 
                    debugging. In addition, he continuously works on his skill set by learning new technologies and approaching new problems.
                     In the summer months George gives back to the community by holding a one month code camp for the Science Institute of Discovery. 
                     During George's free time he plays the guitar and basketball. His philosophy on religion is <i>"Live and Let Live"</i>. </p>
                </div>
            </>
        );
};

export default AboutMe;