import React from 'react';
import SIDwork from '../assets/Sidwork.png';

const PersonalIntrests = () => {
        return (
            <>
                <div id ='community'>
                <h2> Community Work </h2> 
                <img alt="Co-Director" className="ui top Medium left floated image" src={SIDwork}/>
                <p>I have had the privilege to work with the Science Institute of Discovery as Co-Director. The institution has 
                    been running for over 15 years. We have been able to provide stipends and an educational summer experience. 
                    We expose minority and at-risk kids to different fields of Science. Currently, I have been teaching 
                    the coding class during the summer at the institution.</p>
                </div>
            </>
        );
};

export default PersonalIntrests;